import * as React from 'react';
import ReactDOM from 'react-dom';
import { Controller } from '@ali/4ever-cangjie';
import Toolbar from './components/toolbar';
import { usePlugins } from '../PluginsContext';
import { MobileToolbarProps } from './types';

export function MobileToolbar(props: MobileToolbarProps) {
  const controller = Controller.useController();
  const plugins = usePlugins();
  if (!plugins) {
    return null;
  }
  return ReactDOM.createPortal(
    <Toolbar {...props} controller={controller} plugins={plugins} />,
    window.document.body,
  );
}
